import {publicing} from '../../api/api.js'
import {getOrderById,removeOrderById} from '../../api/request.js'
export default {
	data() {
		return {
			message:{}
		}
	},
	methods: {
		init(e){
			publicing(getOrderById,e)
			.then((res)=>{
				console.log(res)
				this.message=res.data;
			})
			.catch((err)=>{
				
			})
		},
	},
	onLoad(option) {
		console.log(option.id);
		this.init(option.id)
	}
}
